        const lefth = Math.floor(lefttime / (1000 * 60 * 60) % 24)
        const leftm = Math.floor(lefttime / (1000 * 60) % 60)
        const lefts = Math.floor(lefttime / 1000 % 60)
        this.time = '' + lefth + ':' + leftm + ':' + lefts
