<template>
  <div class="wait_ma">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="font-size:30px;color: #4b494a"></span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="gotoAnswer"></el-button>
      </div>
      <div class="text item" style="font-size:20px;color: #909399">
        <span style="margin-right: 50px">:</span>    {{username}}
      </div>
      <div class="text item" style="font-size:20px;color: #909399">
        <span style="margin-right: 70px">:</span>    {{qid}}
      </div>
      <div class="text item" style="font-size:20px;color: #909399">
        <span style="margin-right: 90px">:</span>    {{subject}}
      </div>
      <div class="text item" style="font-size:20px;color: #909399">
        <span style="margin-right: 50px">:</span>   {{kstime}}
      </div>
      <div class="text item" style="font-size:20px;color: #909399">
        <span style="margin-right: 12px">:</span>   {{qtime}}
      </div>
      <div class="text item" style="font-size:20px;color: #909399">
        <span style="margin-right: 50px">:</span>   
      </div>
    </el-card>
  </div>
</template>

<script>
  import store from '../../store/index'
  import { getSettings, queryUserInfo } from '../api/api'

  export default {
    data () {
      return {
        username: '',
        qid: '',
        subject: '',
        qtime: '',
        qyear: '',
        qmonth: '',
        qday: '',
        qhour: '',
        qmin: '',
        kstime: ''
      }
    },
    beforeMount () {
      this.queryUserInfo1()
    },
    methods: {
      queryUserInfo1 () {
        queryUserInfo().then(res => {
            if (res.data.code === 20000) {
                this.username = res.data.data.username
                this.qid = res.data.data.qid
                store.commit('setUserType', res.data.data.type)
                if (res.data.data.type === 1) {
                  this.subject = ''
                } else if (res.data.data.type === 2) {
                  this.subject = ''
                } else {
                  this.subject = ''
                }
                }
          }
        )
        getSettings().then(res => {
            if (res.data.code === 20000) {
              this.qtime = res.data.data.Qyear + '' + res.data.data.Qmonth + '' + res.data.data.Qday + '' + '    ' +
                           res.data.data.Qhour + '' + res.data.data.Qmin + ''
              this.qyear = res.data.data.Qyear
              this.qmonth = res.data.data.Qmonth
              this.qday = res.data.data.Qday
              this.qhour = res.data.data.Qhour
              this.qmin = res.data.data.Qmin
              this.kstime = res.data.data.Kstime
            } else {
              this.$message({
                message: '!',
                type: 'warning'
              })
            }
          }
        )
      },
      gotoAnswer () {
        // todo:
        const now = new Date()
        const setTime = new Date(parseInt(this.qyear), parseInt(this.qmonth) - 1, parseInt(this.qday), parseInt(this.qhour), parseInt(this.qmin))
        if (now < setTime) {
          this.$message({
            message: '!',
            type: 'warning'
          })
        } else if (now.getTime() - setTime.getTime() > parseInt(this.kstime) * 60000) {
          this.$message({
            message: '!',
            type: 'warning'
          })
        } else {
          if (this.subject == '') {
            this.$router.replace('/answer')
          } else if (this.subject == '') {
            this.$router.replace('/answer2')
          } else {
            this.$router.replace('/answer3')
          }
        }
      }
    }
  }
</script>

